<template>
    <div class="code">
        <headerBar :title="headerNavData" @save="save()"></headerBar>
        <div class="code_top">
          <!-- <img src="../assets/images/pic.png"> -->
          <canvas id="drawing" :style="{width: '1rem', height: '1rem'}" style="border-radius: 50%"></canvas>
          <p>{{message}}</p>
        </div>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left">{{$t('code.name')}}：</span>
                <yd-input slot="right" type="text"  v-model="codename" max="10" :placeholder="$t('placeholder.codename')"></yd-input>
            </yd-cell-item>
            <!-- <yd-cell-item arrow @click.native="gopswod()">
                <span slot="left">{{$t('code.password')}}</span>
            </yd-cell-item> -->
            <div class="bj"></div>
            <yd-cell-item arrow type="label" @click.native="bindphone()">
                <span slot="left">{{$t('code.bindphone')}}</span>
                <span slot="right">{{phone}}</span>
            </yd-cell-item>
            <yd-cell-item arrow type="label" @click.native="skey(1)">
                <span slot="left">{{$t('code.key')}}</span>
            </yd-cell-item>
            <yd-cell-item arrow type="label" @click.native="skey(2)">
                <span slot="left">{{$t('code.keystore')}}</span>
            </yd-cell-item>
        </yd-cell-group>
        <yd-popup v-model="show1" position="center" width="90%">
            <div class="dig">
                
                <p class="ttl">
                    {{padtxt}}
                </p>
                <input type="password" v-model="password">
                <p class="dig_bt">
                    <yd-button @click.native="show1 = false">{{$t('code.cancel')}}</yd-button>
                    <yd-button @click.native="gowritezjc()">{{$t('code.ok')}}</yd-button>
                </p>
            </div>
        </yd-popup>
        <button class="delcode" type="button" @click="delcode()">{{$t('code.delcode')}}</button>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import { getPrivateFun } from '../assets/js/Keytoken.js'
  export default {
    components:{
      "headerBar":headerBar
    },
    data () {
      return {
        codename:this.$route.query.name,
        message:'',
        show1:false,
        password:'',
        padtxt:this.$t('code.rupassword'),
        num:0,
        headerNavData:{
          left:true,
          title:this.$t('mycenter.wallet'),
          right:true,
          bj:'rgba(255,255,255,0)',
          search:false,
          share:false,
          save:true
        },
        phone:''
      }
    },
    created(){
      this.message = this.$route.query.publickey
      this.headerNavData.title = this.$route.query.name
      this.checkbindphone()
    },
    mounted () {
      this.DrawRandomPic(getCookie("publickey").substring(2,42),"drawing")
    },
    methods:{
      //绑定手机
      bindphone(){
        this.$router.push({path:'/bindphone',query:{publickey:this.message,phone:this.phone}})
      },
      //修改密码
      gopswod(){
        this.$router.push({path:'/password',query:{publickey:this.message}})
      },
      //弹出密码框
      skey(num){
        this.show1=true
        this.num = num
      },
      //生成私钥或者 keystore
      gowritezjc(){
        var that = this
        that.show1 = false
        var ctx;
        if (this.num == 1) {
          that.openloading(that.$t('data.Generatingprivatekey'))
        }else if (this.num == 2) {
          that.openloading(that.$t('data.GeneratingKeyStore'))
        }
        var password = this.password
        var publickey = this.$route.query.publickey
        var keystore;
        var data = JSON.parse(getCookie("codelist"))
        for (var i = 0; i < data.length; i++) {
          if (publickey == data[i].publickey) {
              keystore = data[i].keystore
          }
        }
        // keystore = JSON.stringify(keystore)
        //获取私钥
        setTimeout(function(){
          getPrivateFun(keystore,password,function(skey){
          if(skey=="err")
           {
            that.closeloading()
            that.padtxt=that.$t('data.Passworderror')
            setTimeout(function(){
               that.show1 = true
             },500)
          }else{
            that.closeloading()
            if (that.num == 1) {
              that.$router.push({path:'/keystore',query:{key:skey,type:1}})
            }else if (that.num == 2) {
              that.$router.push({path:'/keystore',query:{key:keystore,type:2}})
            }
          }
        });
        },200)
        
      },
      //保存修改的信息
      save(){
        var that = this
        var codename = this.codename
        var publickey = this.$route.query.publickey
        if (codename == "") {
          this.Toast(this.$t('toast.codename'))
          return
        }else{
          var data = JSON.parse(getCookie("codelist"))
          for (var i = 0; i < data.length; i++) {
            if (publickey == data[i].publickey) {
              that.requirewallet(publickey,codename)
              data[i].name = codename
              var codelist = JSON.stringify(data);
              setCookie("codelist",codelist)
              setTimeout(function(){
                that.$router.push({path:'/'})
              },300)
            }
          }
          
        }
      },
      requirewallet(publickey,nick){
        this.axios({
          url:this.Url + 'wallet.ashx',
          method:'post',
          params: {req:9,publicKey:publickey,nick:nick}
        }).then(function (res) {})
      },
      openConfrim() {
          this.$dialog.confirm({
              title: '更新提示',
              mes: "系统已更新，请先 [备份] 钱包再卸载，重新安装",
              opts: (res) => {
                  for (var i = 0; i < data.length; i++) {
                      if (publickey == data[i].publickey) {
                        data.splice(i,1)
                        this.Toast(this.$t('toast.codedel'))
                        if (data.length == 0) {
                          setCookie("publickey","")
                          setCookie("info","")
                          setCookie("codelist",codelist)
                          setCookie("Currency_list","")
                          that.$router.push({path:'/createcode',query:{del:1}})
                        }else{
                          if (getCookie("publickey") ==  publickey) {
                              setCookie("publickey",data[0].publickey)
                              var codelist = JSON.stringify(data);
                              setCookie("codelist",codelist)
                              setCookie("Currency_list","")
                              that.$router.push({path:'/codelist',query:{del:1}})
                          }else{
                            var codelist = JSON.stringify(data);
                            setCookie("codelist",codelist)
                            setCookie("Currency_list","")
                            that.$router.push({path:'/codelist',query:{del:1}})
                          }
                        }
                        break;
                      }
                  }
              }
          });
      },
      //删除钱包
      delcode(){
        var that = this
        var publickey = this.$route.query.publickey
        var data = JSON.parse(getCookie("codelist"))
        this.$dialog.confirm({
            title: that.$t('data.deletewallet'),
            mes: that.$t('data.walletbeforedeleting'),
            opts: (res) => {
                for (var i = 0; i < data.length; i++) {
                    if (publickey == data[i].publickey) {
                      data.splice(i,1)
                      this.Toast(this.$t('toast.codedel'))
                      setTimeout(function(){
                        if (data.length == 0) {
                        setCookie("publickey","")
                        setCookie("info","")
                        setCookie("codelist",codelist)
                        setCookie("Currency_list","")
                        that.$router.push({path:'/createcode',query:{del:1}})
                      }else{
                        if (getCookie("publickey") ==  publickey) {
                            setCookie("publickey",data[0].publickey)
                            var codelist = JSON.stringify(data);
                            setCookie("codelist",codelist)
                            setCookie("Currency_list","")
                            that.$router.push({path:'/codelist',query:{del:1}})
                        }else{
                          var codelist = JSON.stringify(data);
                          setCookie("codelist",codelist)
                          setCookie("Currency_list","")
                          that.$router.push({path:'/codelist',query:{del:1}})
                        }
                      }
                      },1000)
                      
                      
                    }
                }
            }
        });
      },
      checkbindphone(){
        var that = this
        this.axios({
          url:this.Url + 'Wallet.ashx',
          method:'post',
          params: {req:7,pks:this.$route.query.publickey}
        }).then(res=>{
          if(res.data.data){
            that.phone = res.data.data
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.code{
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.yd-textarea>textarea{
  font-size: .36rem!important
}
.code_top{
  padding: 2.1rem 0 .35rem;
  background: url('../assets/images/codebj.png') no-repeat top left;
  background-size: cover;
  text-align: center;
}
.code_top img{
  width: .85rem;
  height: .85rem;
  border-radius: 50%;
  margin-bottom: .35rem
}
.code_top p{
  width: 99%;
  margin: 0 auto;
  white-space: wrap;
  font-size: .24rem;
  color: #fff;
  letter-spacing: 1px
}
.code_top h1{
  font-size: .3rem;
  color: #fff;
}
.code_center{
  text-align: center;
}
.code_center img{
  width: 4rem;
  height: 4rem;
  margin: 1.5rem auto .35rem;
}
.code_center button{
  width: 4rem;
  color: #666;
  font-size: .28rem;
  padding: .15rem 0;
  outline: none;
  border:none;
  background-color: #eaeaea;
  border-radius: 5px;
}
.code_center button:active{
  background-color: #f5f5f5;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
}
.yd-cell-left span:nth-child(2){
  font-size: .29rem!important;
}
.yd-input input{
  font-size: .28rem!important;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
  color: #888; 
  font-size: .28rem!important;
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
  color: #888; 
  font-size: .28rem!important;
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
  color: #888; 
  font-size: .28rem!important;
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
  color: #888; 
  font-size: .28rem!important;
} 
.bj{
  height: .26rem;
  background: #f4f4f4;
  width: 100%
}
.delcode{
  border: none;
  outline: none;
  background: #61a1e7;
  font-size: .28rem;
  width: 60%;
  padding: .15rem;
  color: #fff;
  margin: 0 auto;
  border-radius: 5px;
  position: fixed;
  bottom: .3rem;
  left: 0;
  right: 0;
}
.delcode:active{
  background: #2a79cd;
}
.dig{
  background: #fff;
  padding: .4rem;
  text-align: center;
}
.ttl{
  font-size: .3rem;
  color: #278eed;
}
.dig input{
  width: 100%;
  height: .7rem;
  line-height: .7rem;
  padding: 0 .12rem;
  background: #f4f4f4;
  border:none;
  border-radius: 5px;
  color: #999;
  font-size: .28rem;
  margin: .3rem 0;
}
.dig_bt button{
  margin: 0 .3rem;
  padding: 0 .5rem
}
.dig_bt button:nth-child(1){
  background: #c20919;
}
.dig_bt button:nth-child(2){
  background: #278eed;
}
</style>



